// SASS OTHER  FILES//
body {
    background: #000;
    font-family: "Raleway", sans-serif;
}
.container-fluid {
    max-width: 1200px;
    margin: 0 auto;
    padding: 10px;
    //NAV
    nav {
        background: #000;
        margin-bottom: 15px;
        .logo {
            margin-right: 260px;
        }
        .rectangle {
            margin-left: 15px;
            margin-bottom: 42px;
        }
        .navigation {
            color: #fff;
            float: right;
            list-style: none;
            margin-right: 115px;
            padding-left: 0;
            margin-top: -35px;
            clear: both;
            .navigate {
                display: inline-block;
                text-transform: uppercase;
                font-size: 16px;
                font-weight: 700;
                padding-right: 10px;
                &:hover {
                    color: #286d89;
                    cursor: pointer;
                }
            }
        }
    }
    // SECTION "TALK"
    section#talk {
        background-image: url("../img/blue-background.png");
        background-repeat: no-repeat;
        background-size: cover;
        h3 {
            font-family: "Arial", sans-serif;
            font-weight: 700;
            font-size: 30px;
            color: #fff;
            border: 1px solid brown;
        }
        p {
            font-family: "Arial", sans-serif;
            font-weight: 400;
            font-size: 16px;
            text-align: left;
            color: #fff;
            border: 1px solid red;
        }
    }
}
